.apropos{
    &__collapse{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        margin-top: 38px;
        margin-bottom: 30px;
       &__content{
            margin-bottom: 30px;
            width: 75%;
       } 
    }
}

@media(max-width : 768px){
    .apropos{
        &__collapse{
            width: 98%;
            margin-top: 27px;
        }
    }
}