.header {
  display: flex;
  padding: 45px;
  align-items: center;
  justify-content: space-between;
}
.header_nav {
  display: flex;
  gap: 57px;
}
.header_nav.active {
  text-decoration-line: underline;
}
.header_logo {
  width: 210px;
  height: 68px;
}

@media (max-width: 768px) {
  .header {
    width: 90%;
    padding: 15px;
  }
  .header_logo {
    width: 80px;
  }
}/*# sourceMappingURL=Header.css.map */